@media screen and (max-width: 1350px) {
  .intro {
    bottom: 260px;
    left: 120px;
  }

  .social {
    left: 50%;
    bottom: 100px;
    transform: rotate(360deg), translate(-50%,0);
  }

  .welcome__button {
    top: 570px;
    left: 50%;
    transform: translate(-50%,0);
  }

  .welcome__photo {
    height: 600px;
  }

  .result {
    & .elipse--one {
      left: 90px;
    }

    & .elipse--three {
      right: 110px;
    }

    & .elipse--two {
      right: 150px;
    }

    & .elipse--five {
      left: 30px;
    }

    & .elipse--six {
      right: 280px;
    }

    & .elipse--seven {
      right: 50px;
    }

    & .elipse--ten {
      right: 60px;
    }

    & .elipse--twelve {
      left: 30px;
      top: 250px;
    }
  }
}

@media screen and (max-width: 1150px) {
  .intro {
    bottom: 210px;
    left: 140px;
  }

  .social {
    bottom: 50px;
  }

  .welcome__button {
    top: 490px;
  }

  .welcome__photo {
    height: 520px;
  }

  .welcome {
    padding-top: 350px;
  }
}

@media screen and (max-width: 1000px) {
  .intro {
    bottom: 260px;
    left: 120px;
  }

  .social {
    bottom: 100px;
  }

  .welcome__button {
    top: 460px;
  }

  .welcome__photo {
    height: 490px;
  }

  .feauters__photo {
    right: -1800px;
  }

  .header__text {
    max-width: 550px;
    text-align: center;
  }
}

@media screen and (max-width: 950px) {
  .social {
    bottom: 120px;
  }

  .page-header {
    height: 810px;
  }

  .welcome__button {
    top: 365px;
  }

  .welcome__photo {
    height: 400px;
  }

  .welcome__container {
    top: -200px;
  }

  .page-header__wrapper:nth-child(2) {
    flex-direction: column;
  }

  .features {
    padding: 100px 15px;

    &__photo {
      width: -1900px;
      opacity: 0.4;
    }
  }

  .process {
    padding: 100px 35px;

    &__block {
      position: static;
      margin: 25px 10px;
    }

    &__wrapper {
      max-width: 100%;
    }
  }

  .result {
    & .elipse {
      z-index: -1;
    }
  }

  .page-footer {
    flex-direction: column-reverse;
    align-items: center;

    &__copy {
      margin-top: 50px;
    }
  }

  .testimonials {
    padding-top: 100px;

    &__wrapper {
      flex-wrap: wrap;
      justify-content: space-around;
    }
  }

  .welcome {
    padding-top: 320px;
    padding-bottom: 100px;
  }

  .what-we-do {
    padding-bottom: 100px;

    &__title {
      padding-bottom: 70px;
    }
  }
}

@media screen and (max-width: 820px) {
  .intro {
    bottom: 360px;
    left: 160px;
  }

  .page-header {
    height: 750px;

    &__wrapper {
      flex-direction: column;

      &:nth-child(2) {
        padding-top: 45px;
        padding-left: 70px;
      }
    }

    &__link {
      display: none;
    }

    .page-header {
      height: 850px;
    }

    &__logo {
      margin-bottom: 20px;
    }

    &__background {
      display: none;
    }

    .welcome {
      padding-top: 300px;
      padding-bottom: 100px;
    }
  }

  .social {
    bottom: 200px;
  }

  .welcome__button {
    top: 285px;
  }

  .welcome__photo {
    height: 320px;
  }

  .welcome__container {
    top: -150px;
  }
}

@media screen and (max-width: 660px) {
  .intro {
    bottom: 420px;
    left: 120px;
  }

  .navigation {

    &__container {
      flex-direction: column;
      align-items: center;
    }

    &__list {
      padding-bottom: 10px;
    }
  }

  .info__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .info-massage {
    width: 1000%;
    padding: 35px;
    box-sizing: border-box;
  }

  .info-contact {
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
  }

  .page-footer {
    padding-top: 40px;
  }

  .result {
    padding: 40px 10px 120px;
  }

  .elipse--two,
  .elipse--nine,
  .elipse--eleven,
  .elipse--one,
  .elipse--five,
  .elipse--ten {
    display: none;
  }

  .social {
    bottom: 270px;
  }

  .page-header {
    height: 800px;

    &__wrapper {
      padding-left: 45px;
    }
  }

  .welcome__button {
    top: 215px;
  }

  .welcome__photo {
    height: 250px;
  }

  .welcome__container {
    top: -120px;
  }

  .welcome {
    padding-top: 250px;
  }
}

@media screen and (max-width: 550px) {
  .intro {
    display: none;
  }

  .navigation {
    &__link {
      margin: 0 5px;
    }
  }

  .header__wrapper {
    padding-left: 0 ;
  }

  .social {
    display: none;
  }

  .page-header {
    height: 800px;

    &__title {
      font-size: 45px;
    }
  }

  .welcome__button {
    top: 215px;
  }

  .welcome__photo {
    height: 250px;
  }

  .welcome__container {
    top: -120px;
  }

  .welcome {
    padding-top: 250px;
  }
}

@media screen and (max-width: 421px) {
  .button {
    width: 224px;

  }

  .info-massage {
    padding: 15px;
  }
}
